/* eslint-disable react/prop-types */
import React from 'react';
import { hydrate } from 'react-dom';
import { BrowserRouter } from 'react-router-dom';
import { ensureReady, After } from '@jaredpalmer/after';
import { ThemeProvider } from '@material-ui/core/styles';
import makeInspectable from 'mobx-devtools-mst';

import * as serviceWorker from './serviceWorker';
import RTL from './RTL';
import routes from './routes';
import theme from './theme';
import { setStore, useStore } from './hooks';

function Main({ data }) {
  const store = setStore(window.__STATE);
  const mystore = useStore();

  makeInspectable(mystore);

  React.useEffect(() => {
    const jssStyles = document.querySelector('#jss-server-side');
    if (jssStyles) {
      jssStyles.parentElement.removeChild(jssStyles);
    }
  }, []);

  return (
    <BrowserRouter>
      <RTL>
        <ThemeProvider theme={theme}>
          <After data={data} routes={routes} store={store} />
        </ThemeProvider>
      </RTL>
    </BrowserRouter>
  );
}

ensureReady(routes).then((data) => { return hydrate(<Main data={data} />, document.getElementById('root')); });

if (module.hot) {
  module.hot.accept();
}
serviceWorker.register();
